import { IconComponent, IconPropSize } from '@consta/icons/Icon';
import { format, isValid, isWithinInterval, parse } from 'date-fns';
import { MaskedDate } from 'imask';
import React, { useCallback, useMemo } from 'react';
import { ReactMaskOpts, useIMask } from 'react-imask';

import { TimeOptions } from '##/components/DateTime/helpers';
import { getTimeOptionsKey } from '##/components/DateTime/helpers/getTimeOptionsKey';
import {
  TextFieldPropForm,
  TextFieldPropSize,
  TextFieldPropStatus,
  TextFieldPropView,
} from '##/components/TextField';
import { useMutableRef } from '##/hooks/useMutableRef';
import { PropsWithHTMLAttributes } from '##/utils/types/PropsWithHTMLAttributes';

import {
  getMaskBlocks,
  getPartDate,
  getParts,
  getPartsDate,
  isValidTimeByTimeOptions,
  useStringValue,
} from '../helpers';
import { datePickerErrorTypes, DatePickerPropOnError } from '../types';

type DatePickerFieldTypeTimePropOnChange = (
  value: Date | null,
  props: {
    e: Event;
  },
) => void;

export type DatePickerFieldTypeTimeProps = PropsWithHTMLAttributes<
  {
    className?: string;
    value?: Date | null;
    onChange?: DatePickerFieldTypeTimePropOnChange;
    onError?: DatePickerPropOnError;
    id?: string;
    name?: string;
    disabled?: boolean;
    size?: TextFieldPropSize;
    view?: TextFieldPropView;
    form?: TextFieldPropForm;
    status?: TextFieldPropStatus;
    onFocus?: React.FocusEventHandler<HTMLElement>;
    onBlur?: React.FocusEventHandler<HTMLElement>;
    autoFocus?: boolean;
    placeholder?: string;
    leftSide?: string | IconComponent;
    rightSide?: string | IconComponent;
    readOnly?: boolean;
    required?: boolean;
    tabIndex?: number;
    inputRef?: React.Ref<HTMLTextAreaElement | HTMLInputElement>;
    ariaLabel?: string;
    iconSize?: IconPropSize;
    children?: never;
    format?: string;
    separator?: string;
    minDate?: Date;
    maxDate?: Date;
    focused?: boolean;
    timeOptions?: TimeOptions;
    label?: string;
    labelIcon?: IconComponent;
    caption?: string;
    labelPosition?: 'top' | 'left';
    withClearButton?: boolean;
  },
  HTMLDivElement
>;

type UsePickerProps = {
  value?: Date | null;
  onChange?: DatePickerFieldTypeTimePropOnChange;
  onError?: DatePickerPropOnError;
  timeOptions?: TimeOptions;
  format: string;
  separator: string;
  minDate: Date;
  maxDate: Date;
};

export const usePicker = (props: UsePickerProps) => {
  const {
    value,
    onChange,
    onError,
    timeOptions,
    format: formatProp,
    separator,
    maxDate,
    minDate,
  } = props;
  const onChangeRef = useMutableRef(onChange);
  const valueRef = useMutableRef(value);
  const onErrorRef = useMutableRef(onError);

  const onAccept = useCallback(
    (stringValue: string, maskRef: unknown, e: InputEvent | undefined) => {
      const onChange = onChangeRef.current;
      const value = valueRef.current;

      if (
        value &&
        isValid(value) &&
        format(value, formatProp) === stringValue
      ) {
        return;
      }

      if (stringValue?.length !== formatProp.length && value && e && onChange) {
        onChange(null, { e });
        return;
      }

      if (onChange && e) {
        if (!stringValue) {
          if (value) {
            onChange(null, { e });
          }
          return;
        }

        const formatArray = getParts(formatProp, separator, false);
        const valueArray = getParts(stringValue, separator, false);
        const validArray = formatArray
          .map((marker) => getPartDate(formatArray, valueArray, marker))
          .filter((item) => Boolean(item));

        if (formatArray.length === validArray.length) {
          const date = parse(
            valueArray.join(':'),
            formatArray.join(':'),
            value || new Date(),
          );

          if (!isWithinInterval(date, { start: minDate, end: maxDate })) {
            const [HH, mm, ss] = getPartsDate(
              stringValue,
              formatProp,
              ':',
              false,
              ['HH', 'mm', 'ss'],
            );

            onErrorRef.current?.({
              type: datePickerErrorTypes[0],
              stringValue,
              date,
              HH,
              mm,
              ss,
            });

            if (value) {
              onChange(null, { e });
            }
            return;
          }

          if (!isValidTimeByTimeOptions(date, timeOptions)) {
            const [HH, mm, ss] = getPartsDate(
              stringValue,
              formatProp,
              ':',
              false,
              ['HH', 'mm', 'ss'],
            );

            onErrorRef.current?.({
              type: datePickerErrorTypes[3],
              stringValue,
              date,
              HH,
              mm,
              ss,
            });

            if (value) {
              onChange(null, { e });
            }
            return;
          }

          onChange(date, { e });
        } else if (value) {
          onChange(null, { e });
        }
      }
    },
    [
      minDate?.getTime(),
      maxDate?.getTime(),
      formatProp,
      separator,
      getTimeOptionsKey(timeOptions),
    ],
  );

  const maskBlocks = useMemo(() => getMaskBlocks({ includeDate: false }), []);

  const imaskProps = useIMask<HTMLInputElement, ReactMaskOpts>(
    {
      mask: Date as unknown as MaskedDate,
      pattern: formatProp,
      blocks: {
        HH: maskBlocks.HH,
        mm: maskBlocks.mm,
        ss: maskBlocks.ss,
      },
      lazy: true,
      autofix: true,
      format: (date: Date) => format(date, formatProp),
      parse: (string: string) => parse(string, formatProp, new Date()),
      validate: (string: string) => {
        const formatArray = getParts(formatProp, separator, false);
        const valueArray = getParts(string, separator, false);
        const validArray = formatArray
          .map((marker) => getPartDate(formatArray, valueArray, marker))
          .filter((item) => Boolean(item));

        if (
          formatArray.length === validArray.length &&
          !isValid(
            parse(valueArray.join(':'), formatArray.join(':'), new Date()),
          )
        ) {
          const [HH, mm, ss] = getPartsDate(string, formatProp, ':', false, [
            'HH',
            'mm',
            'ss',
          ]);

          onErrorRef.current?.({
            type: datePickerErrorTypes[1],
            stringValue: string,
            HH,
            mm,
            ss,
          });
          return false;
        }

        return true;
      },
    },
    { onAccept },
  );

  const handleClear = useStringValue(
    value,
    formatProp,
    separator,
    onChangeRef,
    imaskProps,
  );

  return [imaskProps.ref, handleClear] as const;
};
